<template>
    <div>
       <canvas id="canvas"></canvas>
        <el-form ref="form"  label-width="80px">
            <el-form-item>
                    <el-radio-group v-model="user.roleId">
                    <el-radio-button label="超级管理员"></el-radio-button>
                    <el-radio-button label="管理员"></el-radio-button>
                    <el-radio-button label="教师"></el-radio-button>
                    <el-radio-button label="学生"></el-radio-button>
                    </el-radio-group>
            </el-form-item>
            <el-form-item label="账号">
                <el-input v-model="user.name" placeholder="请输入账号"></el-input>
            </el-form-item>
            <el-form-item label="密码">
            <el-input placeholder="请输入密码" v-model="user.password" show-password>{{user.name}}</el-input>
            </el-form-item>
            <el-form-item >
                <el-select v-model="num" placeholder="请选择您的学校">
                <el-option label="广东工业大学" :value="user.schoolId"></el-option>
                </el-select>
                <el-checkbox v-model="user.switch">记住密码</el-checkbox>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="onSubmit(user)">登录</el-button>
                <el-button type="danger">忘记密码</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
import webSocket from '../../api/websocket'
export default {
  data () {
    return {
      form: {},
      user: {
        roleId: 3,
        name: '',
        password: '',
        schoolId: 55,
        switch: true
      },
      num: '请选择你的学校'
    }
  },
  methods: {
    onSubmit: function (user) {
      console.log(webSocket({type: 6, content: '获取学校列表'}))
    }
  }
}
</script>

<style type="text/css">
html{
    height: 100%;
    }
        body{margin: 0;height: 100%;
            background: #fff;overflow: hidden;}
        canvas{display: block;width: 100%;height: 100%;}
        form{
            position: absolute;
            top: 50%;
            left: 50%;
            width: 30%;
            padding: 50px;
            transform: translate(-50%,-50%); /*使元素本身向左移动宽度的一半*/
            background-color: rgba(255,255,255,0.5);
            border-radius: 8%;
            box-shadow: 0 0 12px 1px grey;
    }
    /* .el-select{ -webkit-backface-visibility: hidden; } */
</style>



